<template>
  <div>
    메뉴222

    {{ sample }}
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      sample: 'ffff'
    }
  },
  setup() {},
  created() {
    this.sample = this.$route.query.name
  },
  watch: {
    $route(val) {
      this.sample = this.$route.query.name
    }
  },
  mounted() {},
  unmounted() {},
  methods: {}
}
</script>

